"use client"

import { useEffect, useRef, useState } from "react"
import { Card } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"

interface PatternCanvasProps {
  patternType: string
  primaryColor: string
  secondaryColor: string
  scale: number
  onPatternGenerated?: (imageUrl: string) => void
}

export function PatternCanvas({
  patternType,
  primaryColor,
  secondaryColor,
  scale,
  onPatternGenerated,
}: PatternCanvasProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return

    const ctx = canvas.getContext("2d")
    if (!ctx) return

    try {
      // Clear previous pattern
      ctx.clearRect(0, 0, canvas.width, canvas.height)

      // Set canvas size
      canvas.width = 500
      canvas.height = 500

      // Generate pattern based on type
      switch (patternType) {
        case "stripes":
          drawStripes(ctx, primaryColor, secondaryColor, scale)
          break
        case "grid":
          drawGrid(ctx, primaryColor, secondaryColor, scale)
          break
        case "dots":
          drawDots(ctx, primaryColor, secondaryColor, scale)
          break
        default:
          drawStripes(ctx, primaryColor, secondaryColor, scale)
      }

      // Notify parent component of new pattern
      if (onPatternGenerated) {
        onPatternGenerated(canvas.toDataURL())
      }

      setError(null)
    } catch (err) {
      setError("Error generating pattern. Please try again.")
      console.error("Pattern generation error:", err)
    }
  }, [patternType, primaryColor, secondaryColor, scale, onPatternGenerated])

  return (
    <Card className="p-4 w-full aspect-square max-w-2xl mx-auto">
      {error && (
        <Alert variant="destructive" className="mb-4">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}
      <canvas
        ref={canvasRef}
        className="w-full h-full border rounded-lg"
      />
    </Card>
  )
}

// Pattern drawing functions
function drawStripes(
  ctx: CanvasRenderingContext2D,
  primary: string,
  secondary: string,
  scale: number
) {
  const width = ctx.canvas.width
  const height = ctx.canvas.height
  const stripeWidth = Math.max(10, 50 * (scale / 100))

  ctx.fillStyle = secondary
  ctx.fillRect(0, 0, width, height)

  ctx.fillStyle = primary
  for (let x = 0; x < width; x += stripeWidth * 2) {
    ctx.fillRect(x, 0, stripeWidth, height)
  }
}

function drawGrid(
  ctx: CanvasRenderingContext2D,
  primary: string,
  secondary: string,
  scale: number
) {
  const width = ctx.canvas.width
  const height = ctx.canvas.height
  const gridSize = Math.max(10, 50 * (scale / 100))

  ctx.fillStyle = secondary
  ctx.fillRect(0, 0, width, height)

  ctx.fillStyle = primary
  for (let x = 0; x < width; x += gridSize) {
    for (let y = 0; y < height; y += gridSize) {
      if ((x + y) % (gridSize * 2) === 0) {
        ctx.fillRect(x, y, gridSize, gridSize)
      }
    }
  }
}

function drawDots(
  ctx: CanvasRenderingContext2D,
  primary: string,
  secondary: string,
  scale: number
) {
  const width = ctx.canvas.width
  const height = ctx.canvas.height
  const spacing = Math.max(20, 100 * (scale / 100))
  const radius = spacing / 4

  ctx.fillStyle = secondary
  ctx.fillRect(0, 0, width, height)

  ctx.fillStyle = primary
  for (let x = radius; x < width; x += spacing) {
    for (let y = radius; y < height; y += spacing) {
      ctx.beginPath()
      ctx.arc(x, y, radius, 0, Math.PI * 2)
      ctx.fill()
    }
  }
}
