"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Slider } from "@/components/ui/slider"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

interface PatternControlsProps {
  onUpdate: (settings: {
    patternType: string
    primaryColor: string
    secondaryColor: string
    scale: number
  }) => void
  onDownload: () => void
}

export function PatternControls({ onUpdate, onDownload }: PatternControlsProps) {
  const [settings, setSettings] = useState({
    patternType: "stripes",
    primaryColor: "#6d28d9",
    secondaryColor: "#ffffff",
    scale: 50,
  })

  const handleUpdate = (newSettings: Partial<typeof settings>) => {
    const updatedSettings = { ...settings, ...newSettings }
    setSettings(updatedSettings)
    onUpdate(updatedSettings)
  }

  return (
    <Card className="p-6 space-y-6">
      <div className="space-y-4">
        <div className="space-y-2">
          <Label>Pattern Type</Label>
          <Select
            value={settings.patternType}
            onValueChange={(value) => handleUpdate({ patternType: value })}
          >
            <SelectTrigger>
              <SelectValue placeholder="Select pattern type" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="stripes">Stripes</SelectItem>
              <SelectItem value="grid">Grid</SelectItem>
              <SelectItem value="dots">Dots</SelectItem>
            </SelectContent>
          </Select>
        </div>

        <div className="space-y-2">
          <Label>Primary Color</Label>
          <div className="flex items-center space-x-2">
            <input
              type="color"
              value={settings.primaryColor}
              onChange={(e) => handleUpdate({ primaryColor: e.target.value })}
              className="w-10 h-10 rounded cursor-pointer"
            />
            <input
              type="text"
              value={settings.primaryColor}
              onChange={(e) => handleUpdate({ primaryColor: e.target.value })}
              className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
            />
          </div>
        </div>

        <div className="space-y-2">
          <Label>Secondary Color</Label>
          <div className="flex items-center space-x-2">
            <input
              type="color"
              value={settings.secondaryColor}
              onChange={(e) => handleUpdate({ secondaryColor: e.target.value })}
              className="w-10 h-10 rounded cursor-pointer"
            />
            <input
              type="text"
              value={settings.secondaryColor}
              onChange={(e) => handleUpdate({ secondaryColor: e.target.value })}
              className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
            />
          </div>
        </div>

        <div className="space-y-2">
          <Label>Scale</Label>
          <Slider
            value={[settings.scale]}
            onValueChange={(value) => handleUpdate({ scale: value[0] })}
            min={10}
            max={100}
            step={1}
            className="w-full"
          />
          <div className="text-sm text-gray-500 text-right">
            {settings.scale}%
          </div>
        </div>
      </div>

      <div className="flex flex-col space-y-2">
        <Button
          onClick={onDownload}
          className="w-full bg-purple-600 hover:bg-purple-700 text-white"
        >
          Download Pattern
        </Button>
        <Button
          onClick={() => handleUpdate({
            patternType: "stripes",
            primaryColor: "#6d28d9",
            secondaryColor: "#ffffff",
            scale: 50,
          })}
          variant="outline"
          className="w-full"
        >
          Reset
        </Button>
      </div>
    </Card>
  )
}
