"use client"

import { useState } from "react"
import Link from "next/link"
import { v4 as uuidv4 } from "uuid"
import { Button } from "@/components/ui/button"
import { PatternCanvas } from "@/components/PatternCanvas"
import { PatternControls } from "@/components/PatternControls"
import { HistorySidebar } from "@/components/HistorySidebar"

interface Pattern {
  id: string
  imageUrl: string
  patternType: string
  primaryColor: string
  secondaryColor: string
  scale: number
}

export default function PatternGenerator() {
  const [currentPattern, setCurrentPattern] = useState({
    patternType: "stripes",
    primaryColor: "#6d28d9",
    secondaryColor: "#ffffff",
    scale: 50,
  })

  const [patterns, setPatterns] = useState<Pattern[]>([])
  const [currentImageUrl, setCurrentImageUrl] = useState<string>("")

  const handlePatternUpdate = (settings: typeof currentPattern) => {
    setCurrentPattern(settings)
  }

  const handlePatternGenerated = (imageUrl: string) => {
    setCurrentImageUrl(imageUrl)
  }

  const handleDownload = () => {
    // Save to history first
    const newPattern: Pattern = {
      id: uuidv4(),
      imageUrl: currentImageUrl,
      ...currentPattern,
    }
    setPatterns((prev) => [newPattern, ...prev])

    // Create download link
    const link = document.createElement("a")
    link.download = `pattern-${newPattern.id}.png`
    link.href = currentImageUrl
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }

  const handlePatternSelect = (pattern: Pattern) => {
    setCurrentPattern({
      patternType: pattern.patternType,
      primaryColor: pattern.primaryColor,
      secondaryColor: pattern.secondaryColor,
      scale: pattern.scale,
    })
  }

  const handleClearHistory = () => {
    setPatterns([])
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Navigation */}
      <nav className="fixed top-0 w-full bg-white border-b z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/" className="text-xl font-bold text-purple-600">
            Pattern Generator
          </Link>
          <Button variant="ghost" asChild>
            <Link href="/">Exit Editor</Link>
          </Button>
        </div>
      </nav>

      {/* Main Content */}
      <main className="container mx-auto px-4 pt-24 pb-12">
        <div className="grid grid-cols-1 lg:grid-cols-12 gap-6">
          {/* Controls */}
          <div className="lg:col-span-3">
            <PatternControls
              onUpdate={handlePatternUpdate}
              onDownload={handleDownload}
            />
          </div>

          {/* Canvas */}
          <div className="lg:col-span-6">
            <PatternCanvas
              {...currentPattern}
              onPatternGenerated={handlePatternGenerated}
            />
          </div>

          {/* History */}
          <div className="lg:col-span-3">
            <HistorySidebar
              patterns={patterns}
              onPatternSelect={handlePatternSelect}
              onClearHistory={handleClearHistory}
            />
          </div>
        </div>
      </main>
    </div>
  )
}
