"use client"

import { Card } from "@/components/ui/card"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Button } from "@/components/ui/button"

interface Pattern {
  id: string
  imageUrl: string
  patternType: string
  primaryColor: string
  secondaryColor: string
  scale: number
}

interface HistorySidebarProps {
  patterns: Pattern[]
  onPatternSelect: (pattern: Pattern) => void
  onClearHistory: () => void
}

export function HistorySidebar({
  patterns,
  onPatternSelect,
  onClearHistory,
}: HistorySidebarProps) {
  if (patterns.length === 0) {
    return (
      <Card className="p-6">
        <div className="text-center text-gray-500">
          <p className="mb-4">No patterns in history</p>
          <p className="text-sm">Generated patterns will appear here</p>
        </div>
      </Card>
    )
  }

  return (
    <Card className="p-4">
      <div className="flex justify-between items-center mb-4">
        <h3 className="text-lg font-semibold">History</h3>
        <Button
          variant="ghost"
          size="sm"
          onClick={onClearHistory}
          className="text-gray-500 hover:text-gray-700"
        >
          Clear All
        </Button>
      </div>
      
      <ScrollArea className="h-[600px] pr-4">
        <div className="space-y-4">
          {patterns.map((pattern) => (
            <Card
              key={pattern.id}
              className="p-2 cursor-pointer hover:shadow-md transition-shadow"
              onClick={() => onPatternSelect(pattern)}
            >
              <div className="aspect-square w-full mb-2 rounded overflow-hidden">
                <img
                  src={pattern.imageUrl}
                  alt={`Pattern ${pattern.patternType}`}
                  className="w-full h-full object-cover"
                />
              </div>
              <div className="space-y-1 text-sm">
                <div className="font-medium capitalize">{pattern.patternType}</div>
                <div className="flex space-x-2">
                  <div
                    className="w-4 h-4 rounded-full border"
                    style={{ backgroundColor: pattern.primaryColor }}
                  />
                  <div
                    className="w-4 h-4 rounded-full border"
                    style={{ backgroundColor: pattern.secondaryColor }}
                  />
                </div>
              </div>
            </Card>
          ))}
        </div>
      </ScrollArea>
    </Card>
  )
}
